import React from "react";
import styled from "styled-components";

import { Grid, Text } from "../elements";
import { Post, Footer, Header } from "../components";

import { actionCreators as postActions } from "../redux/modules/post";

import { useDispatch, useSelector } from "react-redux";


import theme from "../styles/theme";

const Main = (props) => {
  const { color, border, fontSize } = theme;

  const dispatch = useDispatch();

  const [category, setCategory] = React.useState({
    all: true,
    kr: false,
    cn: false,
    jp: false,
    west: false,
    cafe: false,
    etc: false,
  });

  const post_list = useSelector((state) => state.post.list);

  React.useEffect(() => {
    if (post_list.length === 0) {
      dispatch(postActions.getPostAX());
    }
  }, []);

  return (
    <React.Fragment>
      <Grid minWidth="36rem" maxWidth="36rem" margin="0 auto" border={border.line1}>
        <Grid shape="container">
          <Header {...props}>홈</Header>
          <Footer {...props}></Footer>
        </Grid>
        <Grid is_float="left">
          <Grid height="0.8rem" bg="#f4f4f3" />
          <Grid maxWidth="15rem" height="4rem">
            <Text
              shadow="0rem 0.4rem 0.4rem rgba(0, 0, 0, 0.25)"
              size="1.6rem"
              color={color.bg100}
              padding="0.8rem 0"
              margin="0 0 0 2.1rem"
              bold
            >
              #오늘의 인기 메뉴
            </Text>
          </Grid>
          <MainBanner />
          <Grid>
            <Text
              size="1.3rem"
              bold
              shadow="0rem 0.4rem 0.4rem rgba(0, 0, 0, 0.25)"
              text_align="center"
              margin="1rem 0"
            >
              1위&nbsp;<span style={{ fontWeight: "400" }}>#떡볶이</span>
            </Text>
          </Grid>
          <Grid
            maxWidth="3.4rem"
            height="0.6rem"
            bg="orange"
            margin="0 auto 1rem auto"
          />
          <Grid height="0.8rem" bg="#f4f4f3" />
        </Grid>
        <Grid
          maxWidth="33.3rem"
          height="3.2rem"
          margin="0 auto"
          flex
          flex_direction="row"
          align_items="center"
          padding="2rem 0"
        >
          <Text
            width="3rem"
            height="2.4rem"
            size={fontSize.base}
            bold
            margin="0 0.9rem"
            border_bottom={category.all ? "0.1rem solid black" : ""}
            _onClick={() => {
              setCategory({ ...{ category: false }, all: true });
            }}
          >
            전체
          </Text>
          <Text
            width="3rem"
            height="2.4rem"
            size="1.6rem"
            bold
            margin="0 0.9rem"
            border_bottom={category.kr ? "0.1rem solid black" : ""}
            _onClick={() => {
              setCategory({ ...{ category: false }, kr: true });
            }}
          >
            한식
          </Text>
          <Text
            width="3rem"
            height="2.4rem"
            size="1.6rem"
            bold
            margin="0 0.9rem"
            border_bottom={category.cn ? "0.1rem solid black" : ""}
            _onClick={() => {
              setCategory({ ...{ category: false }, cn: true });
            }}
          >
            중식
          </Text>
          <Text
            width="3rem"
            height="2.4rem"
            size="1.6rem"
            bold
            margin="0 0.9rem"
            border_bottom={category.jp ? "0.1rem solid black" : ""}
            _onClick={() => {
              setCategory({ ...{ category: false }, jp: true });
            }}
          >
            일식
          </Text>
          <Text
            width="3rem"
            height="2.4rem"
            size="1.6rem"
            bold
            margin="0 0.9rem"
            border_bottom={category.west ? "0.1rem solid black" : ""}
            _onClick={() => {
              setCategory({ ...{ category: false }, west: true });
            }}
          >
            양식
          </Text>
          <Text
            width="3rem"
            height="2.4rem"
            size="1.6rem"
            bold
            margin="0 0.9rem"
            border_bottom={category.cafe ? "0.1rem solid black" : ""}
            _onClick={() => {
              setCategory({ ...{ category: false }, cafe: true });
            }}
          >
            카페
          </Text>
          <Text
            width="3rem"
            height="2.4rem"
            size="1.6rem"
            bold
            margin="0 0.9rem"
            border_bottom={category.etc ? "0.1rem solid black" : ""}
            _onClick={() => {
              setCategory({ ...{ category: false }, etc: true });
            }}
          >
            기타
          </Text>
        </Grid>
        <Hr />
        <Grid is_flex2 maxWidth="32rem" margin="1rem auto">
          <Grid>
            <Text size="1.3rem" color="#9A9896" bold2="500">
              정렬 기준
            </Text>
          </Grid>
          <Grid flex justify_content="flex-end">
            <Text size="1.3rem" color="#ff9425" bold>
              마감임박순
            </Text>
            <Text size="1.3rem" color="#cecac7" bold margin="0 0 0 1rem">
              최신순
            </Text>
          </Grid>
        </Grid>

        {post_list.map((p, idx) => {
          if (category.all) {
            return <Post {...p} key={p.post_id} />;
          }
          if (category.kr && p.category === "한식") {
            return <Post {...p} key={p.post_id} />;
          }
          if (category.cn && p.category === "중식") {
            return <Post {...p} key={p.post_id} />;
          }
          if (category.jp && p.category === "일식") {
            return <Post {...p} key={p.post_id} />;
          }
          if (category.west && p.category === "양식") {
            return <Post {...p} key={p.post_id} />;
          }
          if (category.cafe && p.category === "카페") {
            return <Post {...p} key={p.post_id} />;
          }
          if (category.etc && p.category === "기타") {
            return <Post {...p} key={p.post_id} />;
          }
          return null;
        })}
        <Grid height="6rem" />
      </Grid>
    </React.Fragment>
  );
};

Main.defaultProps = {};

const MainBanner = styled.div`
  max-width: 32rem;
  height: 10rem;
  margin: 0 auto;
  border-radius: 1rem;
  background-image: url("data:image/jpeg;base64,");
  background-size: cover;
`;

const Hr = styled.hr`
  width: 36rem;
  background-color: #f4f4f3;
  border: 0.1rem solid #f4f4f3;
  margin: 0;
`;
export default Main;
